  display: block;
}

.nav_item {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav_item:not(:first-child) {
  margin-left: 20px;
}

.nav_link {
  text-transform: uppercase;
  color: #000;
  white-space: nowrap;
  justify-content: center;
